.linked-list-wrapper {
  & > .list-wrapper {
    display: flex;
    & > .linked-list-action {
      width: 300px;
    }
    & > .linked-list-view {
      padding-left: 20px;
      width: 100%;
      max-width: 100%;
      max-height: calc(100vh - 120px);
      overflow: auto;
      & > .linked-list {
        &:first-child {
          margin-top: 0px;
        }
      }
    }
  }
}

.list-textfield {
  width: 100%;
  margin-bottom: 20px !important;
}

.linked-list {
  display: flex;
  list-style: none;
  margin-top: 20px;
  margin-bottom: 20px;
  & > .list-node {
    padding: 10px 40px 10px 10px;
    font-size: 16px;
    border: solid 1px #ddd;
    margin-right: 25px;
    position: relative;
    min-width: 100px;
    text-align: center;
    &:last-child {
      & .arrow {
        &::after {
          content: "NULL";
          position: absolute;
          top: -8px;
          font-size: 14px;
          left: 45px;
          color: red;
          font-weight: bold;
        }
      }
    }
    & > .next-tracker {
      position: absolute;
      top: 0px;
      right: 0px;
      height: 100%;
      width: 30px;
      background-color: #ddd;
      & > .arrow {
        width: 35px;
        margin: auto;
        position: absolute;
        left: 15px;
        top: calc(50% - 2px);
        & > .line {
          margin-top: 0px;
          width: 35px;
          background: #777;
          height: 2px;
          float: left;
        }
        & > .point {
          width: 0;
          height: 0;
          border-top: 5px solid transparent;
          border-bottom: 5px solid transparent;
          border-left: 5px solid #777;
          position: absolute;
          right: -5px;
          top: -4px;
        }
      }
    }
    &.current-list-node {
      background-color: #b2dfdb;
      border-color: #b2dfdb;
      font-weight: 600;
      & > .options {
        transition: ease-in 5s;
        display: none;
      }
      &:hover {
        background-color: #b2dfdb;
        border-color: #26a69a;
        & > .next-tracker {
          & > .arrow {
            & > .line {
              background: #26a69a;
            }
            & > .point {
              border-left: 5px solid #26a69a;
            }
          }
        }
        & > .options {
          transition: ease-in 5s;
          position: absolute;
          width: 100%;
          height: 100%;
          background-color: rgba(224, 242, 241, 0.87);
          display: flex;
          justify-content: center;
          align-items: center;
          top: 0;
          left: 0;
          text-align: center;
          & > .delete {
            cursor: pointer;
          }
        }
      }
    }
  }
}
